@include font("OpenSans-Regular", OpenSans-Regular-webfont);
@include font("OpenSans-Bold", OpenSans-Bold-webfont);
@include font("OpenSans-Light", OpenSans-Light-webfont);
@include font("OpenSans-BoldItalic", OpenSans-BoldItalic-webfont);
@include font("OpenSans-Italic", OpenSans-Italic-webfont);
@include font("OpenSans-ExtraBold", OpenSans-ExtraBold-webfont);

// +font("OpenSans-ExtraBoldItalic", OpenSans-ExtraBoldItalic-webfont)
// +font("OpenSans-LightItalic", OpenSans-LightItalic-webfont)
// +font("OpenSans-Semibold", OpenSans-Semibold-webfont)
// +font("OpenSans-SemiboldItalic", OpenSans-SemiboldItalic-webfont)

@mixin f($name, $font-size: null, $letter-spacing: null) {
  $font-family: null;
  @if $name == "r" {
    $font-family: "OpenSans-Regular"; }
  @if $name == "b" {
    $font-family: "OpenSans-Bold"; }
  @if $name == "bi" {
    $font-family: "OpenSans-BoldItalic"; }
  @if $name == "be" {
    $font-family: "OpenSans-ExtraBold"; }
  @if $name == "l" {
    $font-family: "OpenSans-Light"; }
  @if $name == "i" {
    $font-family: "OpenSans-Italic"; }

  font-family: $font-family;
  font-weight: normal;
  @if $font-size != null {
    font-size: $font-size + px; }
  @if $letter-spacing != null {
    letter-spacing: ($letter-spacing / 1000) + em; } }

body {
  @include f(l, 14);
  line-height: 1.4; }
